  color: #595858;
}

.IconBoxes .icon-box:hover .entry-content p,
.IconBoxes .icon-box:hover h3{
  color: #fff;
}
.IconBoxes .icon-box:hover{
  background: #ff4800;
  color: #fff;
}

.IconBoxes .icon-box:hover figure img:nth-of-type(1),
.IconBoxes .icon-box figure img:nth-of-type(2) {
  display: none;
}

.IconBoxes .icon-box:hover figure img:nth-of-type(2) {
  display:block;
}



