.ddwn{
    display: inline-block;
    position: absolute;
    right: 0;
    top:35px;
    cursor: pointer;
}

.ddwn:after{
    content: " ";
    border-top: 5px solid #262626;
    border-left: 5px solid transparent;
    border-right: 5px solid transparent;
}

.dropdown-menu.DropOpen{
    display: block;
}

.MenuTrigger{
    display: block;
    position: absolute;
    top: 30px;
    right: 0;
    width: 24px;
    height: 22px;
    margin: 0 auto;
    transition: .5s ease-in-out;
    cursor: pointer;
}

.MenuTrigger span {
    display: block;
    position: absolute;
    height: 2px;
    width: 100%;
    background: #262626;
    border-radius: 10px;
    opacity: 1;
    left: 0;
    transition: .25s ease-in-out;
}

.MenuTrigger span:nth-child(1) {
    top: 2px;
}

.MenuTrigger span:nth-child(2), 
.MenuTrigger span:nth-child(3) {
    top: 10px;
}

.MenuTrigger span:nth-child(4) {
    top: 18px;
}


.MenuOpen .MenuTrigger span:nth-child(1) {
    top: 18px;
    width: 0;
    left: 50%;
}

.MenuOpen .MenuTrigger span:nth-child(2) {
    transform: rotate(45deg);
}

.MenuOpen .MenuTrigger span:nth-child(3) {
    transform: rotate(-45deg);
}

.MenuOpen .MenuTrigger span:nth-child(4) {
    top: 18px;
    width: 0;
    left: 50%;
}








